
@charset "UTF-8";
@font-face {
  font-family: "avantgarde-book";
  src: url("assets/fonts/Poet(2).eot");
  src: url("assets/fonts/Poet(2).eot?#iefix") format("embedded-opentype"), url("assets/fonts/Poet(2).ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
html,
button,
input,
select,
textarea {
  color: #222;
}
html {
  font-size: 1em;
  line-height: 1.4;
}
::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
.clearfix {
  display: block;
  clear: both;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}
.clearfix:after {
  clear: both;
}
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}
audio,
canvas,
img,
svg,
video {
  vertical-align: middle;
}
fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}
textarea {
  resize: vertical;
}
.ir {
  background-color: transparent;
  border: 0;
  overflow: hidden;
}
.ir:before {
  content: "";
  display: block;
  width: 0;
  height: 150%;
}
.hidden {
  display: none !important;
  visibility: hidden;
}
.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
.invisible {
  visibility: hidden;
}
.browsehappy {
  margin: 0.2em 0;
  background: #ccc;
  color: #000000;
  padding: 0.2em 0;
}
a {
  text-decoration: none;
  transition: all 0.2s linear;
}
a:hover {
  -webkit--transform: scale(1);
  -moz--transform: scale(1);
  -o--transform: scale(1);
  -ms--transform: scale(1);
  transform: scale(1);
}
a:active, a:focus {
  color: #fff;
  text-decoration: none;
}
h1,
h2,
h3 {
  display: block;
  font-family: "avantgarde-book", arial, san-serif;
}
img {
  width: 100%;
  height: 100%;
}
body {
  background-image: url("assets/images/images/travel-agency-website-bg.png");
  background-repeat: no-repeat;
  background-position: left bottom;
  background-attachment: fixed;
  background-size: cover;
  margin: 0px;
  background-color: #5fa5e7;
  padding: 0px;
  font-size: 100%;
  line-height: 1;
  font-family: "avantgarde-book", arial, san-serif;
}
@font-face {
  font-family: "avantgarde-book";
  src: url("assets/fonts/Poet(2).eot");
  src: url("assets/fonts/Poet(2).eot?#iefix") format("embedded-opentype"), url("assets/fonts/Poet(2).ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
